#interface {
  margin-left: 15px;
  width: 100%;
  @each $type in bank, power {
    ##{$type} {
      text-align: center;
      ##{$type}-switch {
        margin: auto;
        background-color: black;
        width: 42px;
        height: 15px;
        border: 5px solid;
        margin-top: -17px;
      }
    }
  }
  @for $i from 1 through 2 {
    #switch_#{$i} {
      cursor: pointer;
      height: 100%;
      background-color: blue;
      width: 50%;
    }
  }
  #display {
    text-align: center;
    width: 70%;
    padding-top: 10px;
    height: 30px;
    margin: 20px auto auto auto;
    background-color: rgb(128, 128, 128);
  }
  #volume-container {
    width: 80%;
    margin: 30px auto auto auto;
    #volume::-moz-range-thumb {
      background-color: blue;
      border-radius: 2px;
      border: none;
      height: 30px;
    }
    #volume {
      width: 100%;
    }
    #volume::-moz-range-track {
      background-color: rgb(64, 64, 64);
      width: 100%;
      height: 8.5px;
    }
  }
}
