@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body{font-family:"Russo One", sans-serif;background-color:gray}

#drum-machine{width:650px;margin:100px auto auto auto;border:5px solid #ff8000;background-color:#a0a0a0}#main{margin-top:-15px;display:flex}

#icon{margin-top:-1px;padding-top:4px;padding-right:5px;text-align:right;font-size:20px}

#drum-pad-container{margin-left:20px;margin-bottom:20px;display:grid;grid-gap:13px;grid-template-rows:repeat(3, 80px);grid-template-columns:repeat(3, 100px)}.drum-pad{cursor:pointer;font-family:"Russo One";box-shadow:4px 4px 10px #000;color:black;border-radius:5px;border:none;outline:none;background-color:gray;-webkit-tap-highlight-color:transparent}.drum-pad::-moz-focus-inner{border:0}

#interface{margin-left:15px;width:100%}#interface #bank{text-align:center}#interface #bank #bank-switch{margin:auto;background-color:black;width:42px;height:15px;border:5px solid;margin-top:-17px}#interface #power{text-align:center}#interface #power #power-switch{margin:auto;background-color:black;width:42px;height:15px;border:5px solid;margin-top:-17px}#interface #switch_1{cursor:pointer;height:100%;background-color:blue;width:50%}#interface #switch_2{cursor:pointer;height:100%;background-color:blue;width:50%}#interface #display{text-align:center;width:70%;padding-top:10px;height:30px;margin:20px auto auto auto;background-color:gray}#interface #volume-container{width:80%;margin:30px auto auto auto}#interface #volume-container #volume::-moz-range-thumb{background-color:blue;border-radius:2px;border:none;height:30px}#interface #volume-container #volume{width:100%}#interface #volume-container #volume::-moz-range-track{background-color:#404040;width:100%;height:8.5px}

